body {
  margin                 : 0;
  // font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  //   'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  //   sans-serif;
  // font-family: Inter, -apple-system, BlinkMacSystemFont, PingFang SC, Hiragino Sans GB, noto sans, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-family            : -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing : antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

pre {
  margin: 0;
}

.icon-wrapper {
  display    : flex;
  align-items: center;
}

.icon-custom {
  display        : flex;
  align-items    : center;
  justify-content: center;

  svg {
    width : 20px;
    height: 20px;
    fill  : currentColor;
  }

  path {
    fill: currentColor;
  }
}

.svg16 {
  svg {
    width : 16px;
    height: 16px;
  }
}

.canvasWrap {
  position: relative;
}

.canvasFrame {
  width  : 100%;
  height : 100vh;
  height : 100%;
  display: block;
  border : none;
}

.wave-bg {
  position: absolute;
  width   : 100%;
  top     : 0;
  left    : 0;
  z-index : 0;
}

.appmgmt-content {
  width           : 100%;
  // padding-right: 20px;
}

.content-container {
  display       : flex;
  flex-direction: column;
  height        : 100vh;
}

.navbar {
  display         : flex;
  justify-content : space-between;
  position        : relative;
  height          : 64px;
  background-color: #fff;
  box-shadow      : 0 1px 4px #00152914;
  z-index         : 20;
}

.main-content-container {
  display    : flex;
  width      : 100vw;
  // height  : 100vh;
  height     : 100%;
  overflow   : hidden;
}

.side-menu {
  position: relative;
  z-index : 10;

  .ant-menu {
    // background-color: #1f222c;
    // padding-left: 32px;
    height    : 100%;
    min-width : 200px;
    overflow-y: auto;
    overflow-x: hidden;

    >.ant-menu-item {
      padding-left : 48px !important;
      padding-right: 48px !important;
    }
  }

  .ant-menu-item-group-title {
    padding-left: 48px !important;
  }

  .ant-menu-submenu-title {
    padding-left: 48px !important;
  }

  .ant-menu-item-group-title {
    padding-left: 64px !important;
  }

  .ant-menu-item-group-list>.ant-menu-item {
    padding-left: 80px !important;
  }

  .ant-menu-submenu .ant-menu>.ant-menu-item {
    padding-left : 64px !important;
    padding-right: 48px !important;
  }

  .menu-collapsed {
    position: absolute;
    top     : 50%;
    left    : 100%;

    img {
      width: 18px;
    }
  }
}

.mainapp-topmenu {
  display   : flex;
  list-style: none;
  margin    : 0;
  padding   : 0;
  height    : 100%;

  >li {
    cursor           : pointer;
    display          : flex;
    // flex-direction: column;
    align-items      : center;
    justify-content  : center;
    transition       : all .3s;
    padding-right    : 32px;
    padding-left     : 32px;

    &:hover {
      // background: #0084F6;
      background: @primary-1;
      color     : @custom-theme;
    }

    .mainapp-topmenu-icon {
      display  : block;
      margin   : 5px auto;
      font-size: 24px;
      // color: #fcfcfc;
    }

    .mainapp-topmenu-name {
      text-align  : center;
      // color: rgba(255, 255, 255, 0.84);
      font-size   : 14px;
      font-weight : 400;
      line-height : 22px;
      padding-left: 8px;
    }
  }

  .mainapp-item-active {
    color     : #1e1653;
    border-top: 2px solid #1e1653;
  }
}

.mainapp-sidemenu {
  flex               : 0 0 auto;
  width              : 74px;
  list-style         : none;
  margin             : 0;
  padding            : 0;
  background         : #1f222c;
  // background-image: linear-gradient(#1e1653, #c72e4e);
  // height: calc(100vh - 54px);

  >li {
    cursor         : pointer;
    height         : 74px;
    display        : flex;
    flex-direction : column;
    justify-content: center;
    transition     : all .3s;
    color          : #fcfcfc;

    &:hover {
      // background: #0084F6;
      background: #fcfcfc;
      color     : #1e1653;
    }

    .mainapp-sidemenu-icon {
      display  : block;
      margin   : 5px auto;
      font-size: 24px;
      // color: #fcfcfc;
    }

    .mainapp-sidemenu-name {
      text-align : center;
      // color: rgba(255, 255, 255, 0.84);
      font-size  : 12px;
      font-weight: 400;
      line-height: 22px;
      padding-top: 4px;
    }
  }
}

.helpDoc {
  position        : fixed;
  right           : 0;
  top             : calc(50% - 55px);
  background-color: @custom-theme;
  color           : #fff !important;
  font-size       : 12px;
  padding         : 8px;
  border-radius   : 2px 0px 0px 2px;
  z-index         : 9;
  cursor          : pointer;

  img {
    padding-top: 2px;
    width      : 12px;
  }
}

[data-theme='compact'] .site-collapse-custom-collapse .site-collapse-custom-panel,
.site-collapse-custom-collapse .site-collapse-custom-panel {
  margin-bottom: 24px;
  overflow     : hidden;
  background   : #f7f7f7;
  border       : 0px;
  border-radius: 2px;
}

.progress-loading-g {
  display         : block;
  position        : absolute;
  top             : 0;
  left            : 0;
  width           : 100%;
  height          : 2px;
  background-image: linear-gradient(to right, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
  z-index         : 99;
}

.progress-loading-g::-moz-progress-bar {
  background-image: linear-gradient(to right, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
}

.progress-loading-g::-webkit-progress-bar {
  background-image: linear-gradient(to right, #4cd964, #5ac8fa, #007aff, #34aadc, #5856d6, #ff2d55);
}

.progress-loading-g::-webkit-progress-value {
  background: #fff;
  transition: all 1s linear;
}

.taskstatus-edit-container {
  .CodeMirror {
    height: 50px !important;
  }
}

.datasearch-container {
  height: 100%;

  .ant-tabs-nav {
    margin-bottom: 0;
  }

  .ant-tabs {
    height: 100%;
  }

  .ant-tabs-content {
    height: 100%;
  }

  .react-codemirror2 {
    height: 100%;
  }

  .CodeMirror {
    height: 100%;
  }

  .cm-theme-light {
    height: 100%;
  }

  .cm-editor {
    height: 100%;
  }

  .cm-theme {
    height: 100%;
  }
}

#d3Container {
  width           : 100%;
  height          : 100%;
  background-color: #fff;
}

