.blood-container {
  height: 100%;

  .ant-tabs-nav-list {
    margin-left: 16px;
  }

  .ant-tabs-nav {
    margin-bottom: 0 !important;
  }

  .ant-tabs {
    height: 100%;
  }

  .ant-tabs-content {
    height: 100%;
  }

  .ant-tabs-content-holder {
    height: 100%;
  }
