.content-wrapper .text-wrapper {
  float     : none;
  text-align: center;
  left      : 0;
  width     : 100%;
  padding   : 0;

  >p {
    max-width   : 100% !important;
    // padding  : 0 40px;
  }
}

.image-wrapper {
  // width   : 65%;
  // float   : left;
  position: relative;
}

.text-wrapper {
  // width      : 35%;
  // float      : left;
  flex-shrink: 0;
  position   : relative;
  top        : 20%;
  left       : -30px;
  font-family: Lato, @font-family;
  z-index    : 1;
}

.text-wrapper h2,
.text-wrapper-bottom h2 {
  font-size  : 32px;
  color      : @text-color;
  font-weight: normal;
  white-space: nowrap;
}

.text-wrapper p {
  margin     : 10px 0;
  font-size  : 16px;
  line-height: 28px;
  color      : #999;
}

.left-text {
  padding-left: 10%;
  text-align  : left;
}

.right-text {
  padding-right: 10%;
  text-align   : right;
}

.home-wrapper {
  position: relative;
  z-index : 9;
  padding : 64px 128px;
}

.home-wrapper-container {
  max-width: 1400px;
  width    : 100%;
}

.home-title {
  font-size    : 24px;
  color        : @custom-theme;
  position     : relative;
  margin-bottom: 48px;
  display      : inline-block;

  &::after {
    content         : '';
    display         : inline-block;
    position        : absolute;
    bottom          : -12px;
    left            : 25px;
    width           : 50px;
    height          : 6px;
    background-color: @custom-theme;
  }
}

.home-page-bg {
  background-color: rgb(247, 249, 252);
}

.ant-upload-list {
  display: inline-block;
}

